<template>
    <div class="landing">
        <div class="landingBody">
            <div class="landingBMain">
                <div class="landingMLeft">
                    <h4><img src="./img/list.svg"/>Categories</h4>
                    <div>
                        <span>Personne</span>
                        <span>Téléphone</span>
                        <span>Ordinateur</span>
                        <span>CNI ou documents</span>
                        <span>Appareil electronique</span>
                        <span>Accessoire electronique</span>
                        <span>Meuble</span>
                        <span>Portefeuille</span>
                        <span>Sac</span>
                        <span>Montre</span>
                        <span>Accessoire electronique</span>
                        <span>Vetement ou chaussure</span>
                        <span>Appareil electronique</span>
                        <span>Vaissele</span>
                    </div>
                </div>
                <div class="landingMRight"  style="z-index:0">
                    <RCaroussel v-show="!connected" style="z-index:-1"/>
                    <div class="landingToForm">
                        <div class="landingTFLeft">
                            <div class="landingTFElt">
                                <span>Vous avez perdu ?</span>
                                <div>Augmentez vos chance de retrouver ce que vous avez perdu. </div>    
                                <div><span @click="$refs.stepsFormLost.Display=true">continuez</span></div>
                            </div>
                        </div>
                        <div class="landingTFLeft">
                            <div class="landingTFElt">
                                <span>J'ai retrouvé !</span>
                                <div>Permetez à un semblable de retrouver facilement son bien! </div>    
                                <div><span @click="$refs.stepsFormFound.Display=true">continuez</span></div>
                            </div>
                        </div>
                    </div>



                <div class="landingMRightA" v-show="connected">
                    <div v-show="eltsFound.length > 0">
                        <span>Mes objects trouvés</span>
                        <div v-for="(found,index) in eltsFound" :key="index" class="landingMRElt">
                            <div class="landingMREltInfos">
                            <div class="landingMREltImg">

                            </div>
                            <div>
                                <span class="landingMRCity">{{ found.description.slice(0,25) }}</span>
                                <span>{{ found.city+', '+found.quater }}</span>
                                <span>{{ found.type }}</span>
                            </div>
                            </div>
                            <div class="landingMREltMenu">
                                <div class="landingMRElTFermer">Fermer</div>
                                <div @click="edit(found)">Editer</div>
                                <div @click="deleteMe(eltsFound,index)">Suppr</div>    
                            </div>
                        </div>
                    </div>

                    <div v-show="eltsLost.length > 0">
                        <span>Mes avis de recherche</span>
                        <div v-for="(lost,index) in eltsLost" :key="index" class="landingMRElt">
                            <div class="landingMREltInfos">
                            <div class="landingMREltImg">

                            </div>
                            <div>
                                <span class="landingMRCity">{{ lost.description.slice(0,25) }}</span>
                                <span>{{ lost.city+', '+lost.quater }}</span>
                                <span>{{ lost.type }}</span>
                            </div>
                            </div>
                            <div class="landingMREltMenu">
                                <div class="landingMRElTFermer">Fermer</div>
                                <div @click="edit(lost)" >Editer</div>
                                <div @click="deleteMe(eltsLost,index)">Suppr</div> 
                            </div>
                        </div>
                    </div>
                
                </div>



                </div>
            </div>
        </div>
        <JFooter/>
        <JNavbar 
            @inscription='$refs.inscription.Display=true'
            @connexion='$refs.connexion.Display=true'
            @iLost='$refs.stepsFormLost.Display=true'
            @iFound='$refs.stepsFormFound.Display=true'
        />            
        <StepsForm ref='stepsFormLost' type="lost" @connexion='$refs.connexion.Display=true' @newElt='getObjects'/>
        <StepsForm ref='stepsFormFound' type="found" @connexion='$refs.connexion.Display=true' @newElt='getObjects'/>
        <StepsForm ref='stepsFormEditF' type="found" edit="yes"/>
        <StepsForm ref='stepsFormEditL' type="lost" edit="yes"/>
        <Inscription ref='inscription' @connexion='$refs.connexion.Display=true'/>
        <Connexion ref='connexion' @inscription='$refs.inscription.Display=true'/>
        <LLoading1 :visible='loading'/>
    </div>
</template>
<style src="./landing.css">
</style>
<script src = "./landing.js">
</script>
